<script>
  export default {
    data () {
      return {
        selectNum: false,
        num: 0
      }
    },
    computed: {
      invoice_piao: {
        get () {
          return this.$store.state.form.red_form
        }
      },
      month: {
        get () {
          let a = this.$store.state.service.serviceData.kaipiao_time.slice(5, 7)
          return a
        }
      }
    },
    methods: {
      select (i) {
        this.selectNum = i
      },
      dbSelectPop (i) {
        this.num = i
        this.$refs.pop.private_confirm()
      },
      click_red_upload () {
        if (!this.selectNum && this.selectNum !== 0) {
          layer.msg('请选中上传')
        } else {
          this.$store.commit('uploadOneRedData', this.selectNum)
          layer.msg('上传成功')
        }
      }
    }
  }
</script>

